import React from 'react';
import styles from './factopedia.module.css';

//import Logo from './img/adblogobf.png';

/////////////////////////////////////////
class Accordion extends React.Component {
  render() {
    const { title, expand, onClick, name } = this.props;
    
    return (
      <div>
            <div className={expand ? [styles.title, styles.is_expanded].join(" ") : styles.title } onClick={onClick}> {title} </div>
            <div className={expand ? [styles.content, styles.is_expanded].join(" ") : styles.content } onClick={onClick}>
                <p> {name} </p>
            </div>
      </div>
    );
    
  }
}
class Application extends React.Component {  
  constructor (props) {
    super(props);
    this.state = {
      block1: true,
      block2: false,
      block3: false,
    };
  }
  
  toggle = (index) => () => {
    this.setState({ [`block${index}`]: !this.state[`block${index}`] });
  }
  
  toggleExpand = (expand = false) => () => {
    this.setState({
      block1: expand,
      block2: expand,
      block3: expand,
    });
  }
  
  render() {
      const accordionList = [
          {
              title: 'First Accordion',
              name: 'doberman'
          },
          {
              title: 'Second Accordion',
              name: 'rottwaller'
          },
          {
              title: 'Third Accordion',
              name: 'chiwawa'
          }];
    
      return (
          <div className={styles.container}>
            <h3>Factopedia</h3>
              <button type="button" className={styles.Button} onClick={this.toggleExpand(true)}>Expand All</button>
              <button type="button" className={styles.Button} onClick={this.toggleExpand()}>Collapse All</button>
              <div className={styles.accordion}>
            {
                accordionList.map((item, index) => (
                    <Accordion
                        key={item.name}
                        title={item.title}
                        name={item.name}
                        onClick={this.toggle(index + 1)}
                        expand={this.state[`block${index + 1}`]}
                    />                
                ))
            }
            </div>
        </div>
      );
  }
}

class Aside extends React.Component {

    render() {
        
        const Fragment = React.Fragment;
        return (
            <Fragment>               
                <aside className={styles.Aside}>                       
                    < Application />
                </aside>
            </Fragment>
        );
    }
}

export default Aside;

