import React from 'react';
import styles from './factopedia/factopedia.module.css';
import Aside from './factopedia/aside.js';
import dogs from './dogs.json';
import TestView from './test.js';
 

class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }

  componentDidMount() {
      fetch(dogs)
      //.then(res => res.text())
          .then(res => res.json())
         // .then(text => console.log());
        .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result.items
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

    render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
        console.log(items)
      return <div>Loading...</div>;
    } else {
      return (
        <ul>
          {items.map(item => (
            <li key={item.id}>
              {item.name} {item.dog_function}
            </li>
          ))}
        </ul>
      );
    }
  }
}

